.trd-logo-color {
  color: #f00; }

.newsletter.widget-embed {
  background-color: #000;
  color: #f1f1f1;
  margin-bottom: 16px; }

.newsletter.widget-dark .widget-embed-form {
  background-color: #000; }
  .newsletter.widget-dark .widget-embed-form input {
    border: none; }

.newsletter.widget-large .widget-embed-title {
  font-size: 16px; }

.newsletter.widget-fixed {
  transition: all ease-in-out 300ms;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  right: -500px;
  width: 350px; }
  .newsletter.widget-fixed.widget-large .widget-embed-title {
    font-size: 22.4px; }
  .newsletter.widget-fixed.show {
    right: 0; }
  .newsletter.widget-fixed .widget-embed-header {
    position: relative; }
  .newsletter.widget-fixed .widget-embed-title {
    text-align: right;
    margin-right: 10px; }
  .newsletter.widget-fixed .widget-embed-description {
    font-size: 24px; }
  .newsletter.widget-fixed .widget-embed-close {
    display: inline-block;
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    padding: 8px 10px; }
    .newsletter.widget-fixed .widget-embed-close .fa {
      font-size: 32px; }
  @media only screen and (max-width: 781px) {
    .newsletter.widget-fixed {
      width: 100%;
      top: auto;
      transform: none; }
      .newsletter.widget-fixed.show {
        right: 0;
        bottom: 60px; }
      .newsletter.widget-fixed .widget-embed-title {
        text-align: center; } }

.newsletter .widget-embed-header, .newsletter .widget-embed-form {
  padding: 8px; }

.newsletter .widget-embed-title, .newsletter .widget-embed-desctiption {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 12.8px;
  padding: 0;
  margin: 0;
  text-align: center; }

.newsletter .widget-embed-title {
  text-transform: uppercase; }

.newsletter .widget-embed-description, .newsletter .widget-embed-form {
  background-color: #f1f1f1;
  color: #000; }

.newsletter .widget-embed-close {
  display: none; }

.newsletter .widget-embed-description {
  font-size: 16px;
  margin: 0;
  padding: 16px;
  text-align: center; }

.newsletter .widget-embed-form {
  display: flex; }
  .newsletter .widget-embed-form button {
    display: inline-block;
    background-color: #f00;
    text-transform: uppercase;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: center;
    border-radius: 20px;
    border: none;
    min-width: 100px;
    padding: 8px;
    cursor: pointer;
    transition: all ease-in-out 200ms; }
    .newsletter .widget-embed-form button:hover {
      letter-spacing: 1px; }
  .newsletter .widget-embed-form input {
    flex: 1 0 auto;
    background-color: #fff;
    border: none;
    transition: all ease-in-out 200ms;
    display: inline-block;
    border-radius: 20px;
    padding: 8px;
    margin-right: 8px;
    border: 1px solid #999;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    font-size: 16px; }
    .newsletter .widget-embed-form input:focus {
      outline: none;
      box-shadow: 0 0 0 2px #EC1C24; }
